const modernizr = require('./_modernizr'),
	  Flickity  = require('flickity'),
	  // Initialize Javascript Gallery Plugin
	  flkty = new Flickity('#gallery',{
			wrapAround: true,
			freeScroll: true,
			prevNextButtons: true,
			dragTreshold: 10
	  });

// HTML5 Video Background Accessibility Tweaks
(() => {
	const button = document.getElementById('pausevideo');

	if (window.matchMedia('(prefers-reduces-motion)').matches) {
		if (window.getComputedStyle(document.getElementById('bgvideo-mobile')).visibility == 'visible') {
			var vid = document.getElementById('bgvideo-mobile')
		} else {
			var vid = document.getElementById('bgvideo-normal')
		}
		vid.removeAttribute('autoplay');
		vid.pause();
		button.innerHTML = 'play video'
	};
	button.addEventListener('click', () => {
		if (window.getComputedStyle(document.getElementById('bgvideo-mobile')).visibility == 'visible') {
			var vid = document.getElementById('bgvideo-mobile')
		} else {
			var vid = document.getElementById('bgvideo-normal')
		}
		if (vid.paused) {
			vid.play();
			button.innerHTML = 'stop video'
		} else {
			vid.pause();
			button.innerHTML = 'play video'
		}
	});
})();

